    background-color: #02250F;
    
}

.colaboradorasTitle{
    height: 5%;
    display: grid;
    grid-template-rows: 1fr;
    -webkit-justify-content: center;
            justify-content: center;
    padding-top: 2rem;
}

.colaboradorasTitle h1{
    padding: 15px;
    border-radius: 10px;
    color: #02250F;
    font-size: 35px;
    font-family: 'Playfair Display', serif;
    text-transform: uppercase;
    letter-spacing: 2px;
    background-color: #8bb85c;
}

.container{
    padding-left: 10%;
    padding-top: 15rem;
    background-color: #fff;
    display: grid ;
    grid-template-columns: 1fr 1fr;
    -webkit-align-items: center;
            align-items: center;
    background-color: #02250F;
}

.card{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    width: 280px;
    margin: 5rem 2rem;
    border-radius: 50px;
    background: #8bb85c;
   
}

.card .images {
    border-radius: 50px;
    height: 312px;
    -webkit-transform: scale(.65);
            transform: scale(.65);
    -webkit-transform-origin: top;
            transform-origin: top;
    transition: -webkit-transform 0.5s cubic-bezier(.4,0,.2,1);
    transition: transform 0.5s cubic-bezier(.4,0,.2,1);
    transition: transform 0.5s cubic-bezier(.4,0,.2,1), -webkit-transform 0.5s cubic-bezier(.4,0,.2,1);
    transition-delay: .2s;
    width:  400px;
    margin-top: -100px;
}

.imgBox img{
  width: 450px;
}

.imgBox{
    -webkit-clip-path: path("M 390,400 C 390,504.9341 304.9341,590 210,590 95.065898,590 10,504.9341 10,400 V 10 H 200 390 Z");
            clip-path: path("M 390,400 C 390,504.9341 304.9341,590 210,590 95.065898,590 10,504.9341 10,400 V 10 H 200 390 Z"); 
    position: relative;
    -webkit-transform-origin: 50%;
            transform-origin: 50%;
    top: -200px;
}

.content img{
    width: 38px;
}

.circle{
    border-radius: 0%;
    height: 350px;
    left: 10px;
    position: absolute;
    top: 210px;
    width: 480px;
}

.person{
    position: relative;
    left: 2px;
    top: 180px;
    bottom: 0;
    width: 420px;
    -webkit-transform: translateY(20px) scale(1.15);
            transform: translateY(20px) scale(1.15);
    -webkit-transform-origin: 50% bottom;
            transform-origin: 50% bottom;
    transition: -webkit-transform 0.7s cubic-bezier(.4,0.2, 1);
    transition: transform 0.7s cubic-bezier(.4,0.2, 1);
    transition: transform 0.7s cubic-bezier(.4,0.2, 1), -webkit-transform 0.7s cubic-bezier(.4,0.2, 1);
}

.card:hover .person {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

.card:hover .images{
    -webkit-transform: scale(.68);
            transform: scale(.68);
    transition-delay: .2s;
}

.content {
    margin-top: -2.8rem;
    text-align: center;
    overflow: hidden;

}

.content h3{
    font-size: 1.5rem;
    text-transform: uppercase;
    font-weight: 700;
    color: #02250F;

}

.content p {
    display: inline-block;
    font-size: 19px;
    color: #02250F;
    font-weight: 300;
    border-bottom: 2px solid  #77e762;
}

.content .social{

    display: -webkit-flex;

    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    transition: .4s;
}

.content .social a{
    width: 34px;
    height: 34px;
    font-size: 25px;
    line-height: 34px;
    text-align: center;
    border-radius: 6px;
    background: linear-gradient(145deg, #EEFEE2, #EEFEE2);
    box-shadow: 5px 5px 11px rgb(183, 255, 183), -5px -5px 11px #77e762;
    margin: 0 0.8rem;
    color: #041C0D;
    transition: 0.8s;
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
    transition-delay: calc(0.1s  * var(--i));
}

.card:hover .content .social a{
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    margin-top: 1rem;
    cursor: pointer;
}


.content .social a:hover{
    background: #9DDF6C;
    box-shadow: inset 5px 5px 11px #8393a1, inset -5px -5px 11px #fff;
}

.card:hover .content .social {
    padding-bottom: 1rem;
}

